import * as React from "react";
import otterGIF from "../images/Lane7.gif";
import lane7Video from "../static/Lane7_White.mp4";

export default function HeroBrandText() {
  return (
    <div className="container-fluid">
      <div
        className={`row p-4 mt-4 text-white bg-dark rounded`}
        style={{ fontFamily: "Playfair Display" }}
      >
        <div className="col">
          <img
            alt="Lane7"
            src={otterGIF}
            style={{ width: "100%", height: "350px" }}
          />
        </div>
        <div
          className="col"
          style={{ fontFamily: "Playfair Display", fontWeight: 600 }}
        >
          <h2 className="mb-4" style={{ fontWeight: 600 }}>
            Welcome to Lane7
          </h2>
          <h3 className="mb-4">
            We are an independent agency dedicated to the craft of advertising.
            Imagine colour, imagine baffling design concepts, imagine Cristiano
            Ronaldo wearing your brand whilst shopping for another Buggatti !
          </h3>
          <h2 style={{ fontWeight: 600 }}>
            Imagine the Infinite. Imagine Lane7
          </h2>
        </div>
      </div>
      <div
        className={`row p-4 bg-light border-top border-dark rounded`}
        style={{ fontFamily: "Playfair Display" }}
      >
        <div className="col-8">
          <h3 className="mb-4">
            We absorb your ideas and conjure advertising strategies that propel
            your business onto the online universe
          </h3>
          <h3>
            Content creation, web design, brand-building are some things we
            absolutely LOVE. Btw, we donâ€™t just do 3D rendering, animations and
            corporate branding, we make magic!
          </h3>
          <h3>
            We are constantly on the hunt for clients who are serious about
            growing their business. Do you have a challenge for us?
          </h3>
          <div className="text-center">
            <button className="text-center btn btn-lg btn-outline-dark">
              Get in Touch
            </button>
          </div>
        </div>
        <div className="col">
          <video
            src={lane7Video}
            style={{ width: "100%", height: "350px" }}
            autoplay
            loop
            controls
          ></video>
        </div>
      </div>
    </div>
  );
}
