import * as React from "react";
import * as styles from "./heroCarousel.module.css";
import laneImg from "../images/Lane.png";
import seltzerImg from "../images/Seltzer.png";
import sippyImg from "../images/Sippy.png";
import snowLeopardImg from "../images/SnowLeopard.png";
import vivaCiderImg from "../images/VIVA_Cider.png";

export default function HeroCarousel() {
  const widt = 300;
  const heig = 400;
  const items = [
    {
      id: 1,
      fileSrc: laneImg,
      label: "Lane",
      width: widt,
      height: heig,
    },
    {
      id: 2,
      fileSrc: seltzerImg,
      label: "Seltzer",
      width: widt,
      height: heig,
    },
    {
      id: 3,
      fileSrc: sippyImg,
      label: "Sippy",
      width: widt,
      height: heig,
    },
    {
      id: 4,
      fileSrc: snowLeopardImg,
      label: "SnowLeopard",
      width: widt,
      height: heig,
    },
    {
      id: 5,
      fileSrc: vivaCiderImg,
      label: "VIVA Cider",
      width: widt + widt,
      height: heig,
    },
  ];

  return (
    <div className={`row`}>
      <div className="col">
        <div
          className={`${styles.sliderImgContainer} ${styles.sliderImgContainer_hover_pause}`}
        >
          <div className={styles.slider_group}>
            {items.map((item, idx) => {
              return (
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  data-bs-title="Tooltip on top"
                  key={idx}
                >
                  <img
                    alt={item.label}
                    src={item.fileSrc}
                    width={item.widt}
                    height={item.heig}
                  />
                </span>
              );
            })}
          </div>

          <div className={styles.slider_group} aria-hidden="true">
            {items.map((item, idx) => {
              return (
                <span
                  data-bs-toggle="tooltip"
                  data-bs-placement="top"
                  data-bs-title="Tooltip on top"
                  key={idx}
                >
                  <img
                    alt={item.label}
                    src={item.fileSrc}
                    width={item.widt}
                    height={item.heig}
                  />
                </span>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
